.main{
  width:330px;
  height:330px;
  border:1px solid #CCC;
  position:absolute;
  top:0; bottom:0; left:0;right:0;
  margin:auto;
  border-radius:50%; 
}

.circle{
  width:30px;
  height:30px;
  background:rgb(231, 76, 60);
  border-radius:50%;
  position:absolute;
  top:0; bottom:0; left:0;right:0;
  overflow:hidden;
  margin:auto;
  -webkit-animation: circle 6s linear infinite;
          animation: circle 6s linear infinite;
}

@-webkit-keyframes circle{
  0%{
    -webkit-transform:rotate(0deg)
              translate(-165px)
              rotate(0deg);
            transform:rotate(0deg)
              translate(-165px)
              rotate(0deg);
  
  }
  100%{
    -webkit-transform:rotate(360deg)
              translate(-165px)
              rotate(-360deg);
            transform:rotate(360deg)
              translate(-165px)
              rotate(-360deg);
  }
}

@keyframes circle{
  0%{
    -webkit-transform:rotate(0deg)
              translate(-165px)
              rotate(0deg);
            transform:rotate(0deg)
              translate(-165px)
              rotate(0deg);
  
  }
  100%{
    -webkit-transform:rotate(360deg)
              translate(-165px)
              rotate(-360deg);
            transform:rotate(360deg)
              translate(-165px)
              rotate(-360deg);
  }
}


